@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@300;400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Voces&display=swap);
.app__gameList{max-width:1700px;padding:50px 25px 0px 25px;display:grid;grid-template-columns:repeat(auto-fit, minmax(400px, 1fr));grid-gap:80px clamp(15px, 4vw, 100px);margin:0 auto;-webkit-flex-direction:row;flex-direction:row;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:space-between;justify-content:space-between}@media (max-width: 885px){.app__gameList{grid-template-columns:repeat(auto-fit, minmax(310px, 1fr))}}@media (max-width: 480px){.app__gameList{grid-template-columns:repeat(auto-fit, minmax(270px, 1fr));padding:40px 10px 0px 10px}}.navbar{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;background-color:#171a21;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center;box-shadow:0px 22px 38px rgba(0,0,0,0.25);padding:14px 30px}.navbar__logo{font-size:10px;width:150px}.navbar__search{width:clamp(250px, 50vw, 700px);margin-left:20px;height:50px;padding-left:clamp(10px, 2.5vw, 20px);background:#333;box-shadow:0px 0px 7px 5px rgba(6,7,8,0.25);border-radius:6px;border:0px;font-family:"IBM Plex Mono", monospace;font-weight:400;font-size:clamp(19px, 3vw, 23px);letter-spacing:-0.005em;color:#ece6df}.navbar__search:focus{outline:none;background:#3b3b3b}@media (max-width: 480px){.navbar{padding:14px 15px}.navbar__search{display:none}}.game{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;position:relative;-webkit-filter:drop-shadow(19px 10px 14px rgba(0,0,0,0.2));filter:drop-shadow(19px 10px 14px rgba(0,0,0,0.2))}.game__top{position:relative;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;height:200px}.game__top__img{width:100%;height:100%;width:-webkit-fill-available;object-fit:cover;object-position:0% 10%;-webkit-filter:drop-shadow(3px 0px 0px #ffc105);filter:drop-shadow(3px 0px 0px #ffc105);border-radius:6px 200px 0px 0px}.game__top__rating{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;position:absolute;top:15%;right:0px;background:linear-gradient(90deg, #41295a 0%, #2f0743 100%);border-radius:12px}.game__top__rating__star{color:#ffc105;margin-left:7px;font-size:19px}.game__top__rating__text{font-family:"Roboto", sans-serif;font-size:19px;color:#ffc105;border-radius:10px;padding:4px 12px 4px 5px}.game__top__name{position:absolute;width:100%;padding-right:25px;font-size:28px;padding-left:15px;bottom:60px;letter-spacing:1px;text-shadow:2px 2px 1px #1b1a1a;font-family:"Voces", cursive}.game__top__name__text{overflow:hidden;white-space:nowrap;text-overflow:ellipsis;color:#e3e3e3}.game__top__platforms{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;position:absolute;right:15px;-webkit-flex-wrap:wrap;flex-wrap:wrap;bottom:3px;padding-left:15px}.game__top__platforms__platform{font-size:0.9rem;border-radius:6px;margin:1.5px;padding:2px 8px;background-color:#3d1717}.game__bot{padding-top:20px;padding-left:15px;padding-right:40px;background-color:#18233d;box-shadow:3px 0px 0px #ffc105;border-radius:0px 0px 0px 6px;height:200px}.game__bot__text{font-size:1.2rem;letter-spacing:0.1px;word-spacing:5px;font-family:"Staatliches", cursive;color:#e3e3e3;overflow:hidden;text-overflow:ellipsis;display:-webkit-box;-webkit-line-clamp:6;-webkit-box-orient:vertical;text-shadow:2px 2px 1px #1b1a1a}*{box-sizing:border-box}html,body,#root{width:100%;height:100%;margin:0;padding:0;-webkit-font-smoothing:antialiased;background-color:#17172a;font-family:"Roboto", sans-serif;color:white}

